<template>
    <div class="catalogue">

        <van-nav-bar title="目录" left-arrow  class="van-head" @click-left="back">
        </van-nav-bar>

        <div class="catalogue-box">
            <div class="catalogue-box-item clearfix">
                <div class="fl catalogue-box-title">目录</div>
                <div class="fl catalogue-box-add">共{{CatalogueData.length}}章</div>
                <div class="fr catalogue-boxorder"  @click="gettodo">{{isActive ? '倒序':'正序'}}</div>
            </div>
            <div class="catalogue-boxlist">
                <div class="clearfix catalogue-box-list" v-for="(item,index) in CatalogueData" :key="index" @click="skidId(item.link,item.order)">
                    <div class="fl catalogue-box-add">{{item.title}}</div>
                    <div class="fr">
                        <span v-show="item.isVip" class="iconfont icon-suo head-icons"></span>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import '../assets/css/catalogue.less'
export default {
    data(){
        return{
            CatalogueData:{},
            urls:null,
            isActive:true
        }
    },
    created(){
        this.getBookLink();
    },
    methods:{
        //返回
        back(){
            this.$router.go(-1);
        },
         //获取书籍目录
        getBookLink(){

            this.axios({
                method: 'GET',
                url:'http://novel.kele8.cn/book-sources?view=summary&book='+this.$route.query.id
            }).then( result =>{
                if(result.status == 200){
                    this.urls = result.data[0]._id;
                }

                 this.axios({
                    method: 'GET',
                    url:'http://novel.kele8.cn/book-chapters/'+this.urls
                }).then( result =>{
                    if(result.status == 200){
                        this.CatalogueData = result.data.chapters;
                         
                        let url = encodeURIComponent(this.CatalogueData[0].link)
                         
                    }
                }).catch(err => {
                     
                })
            }).catch(err => {
                 
            });
           
            
        },
        //是否倒叙排列
        gettodo(){
            this.isActive = !this.isActive;
            // 将数组倒序
            this.CatalogueData.reverse();
            //  
        },
        //跳转阅读页
        skidId(url,index){
             
            let id = this.id = this.$route.query.id;
             
            this.$router.push({
                name: "Readnow",
                query:{
                    url,
                    id,
                    index:index-1
                }
            })
        },

    }
}
</script>
